html {
    font-size: 10px;
  }
  body {
    background: #ffc600;
    font-family: 'helvetica neue';
    font-weight: 200;
    font-size: 20px;
  }
  .words {
    max-width: 500px;
    margin: 50px auto;
    background: white;
    border-radius: 5px;
    box-shadow: 10px 10px 0 rgba(0,0,0,0.1);
    padding: 1rem 2rem 1rem 5rem;
    background: -webkit-gradient(linear, 0 0, 0 100%, from(#d9eaf3), color-stop(4%, #fff)) 0 4px;
    background-size: 100% 3rem;
    position: relative;
    line-height: 3rem;
  }
  
  p {
    margin: 0 0 3rem;
  }
  .words:before {
    content: '';
    position: absolute;
    width: 4px;
    top: 0;
    left: 30px;
    bottom: 0;
    border: 1px solid;
    border-color: transparent #efe4e4;
  }
  .start{
      padding: 25px;
      background-color: green;
      border: none;
      color: #fff;
  }
  .end{
    padding: 25px;
    color: #fff;
    background-color: #ff3c00;
    border: none;
}
.save {
  border: none;
  margin: 10px 0 0 0;
  padding: 30px;
  background-color: #0000ff;
  color: #fff;

}